div#rotatecircle ul{
	height: 100%;
	width: 100%;
  display: block;
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  justify-content: center;
}

div#rotatecircle li{
	position: absolute;
	display: block;
	background: transparent;
	border: 10px solid rgb(128, 204, 255);
  border-left: 120px solid transparent;
  border-right: 120px solid transparent;
  border-bottom: 28.8675px solid rgb(133, 216, 255);
	border-radius: 200px;
  transition: all 0.5s ease;
  overflow-x: hidden;
}

div#rotatecircle li:first-child{
	margin-left: 20px;
	margin-top: 40px;
	z-index: 2;
	width: 240px;
	height: 240px;
  border-width: 10px;
  border-style: dashed;
  border-left: 10px solid rgb(133, 216, 255);
  border-right: 10px solid rgb(133, 216, 255);
  border-top: 1px solid rgb(9, 90, 128);
  border-bottom: 1px solid rgb(9, 90, 128);
  position: absolute;
	-webkit-animation: spinBG 5s infinite linear;
	        animation: spinBG 5s infinite linear;
}

div#rotatecircle li:nth-child(2){
	margin-left: 15px;
	margin-top: 25px;
	z-index: 1;
	width: 270px;
	height: 270px;
	border-width: 2px;
  border-style: dotted;
  position: absolute;
	box-shadow: 0px 0px 20px #7dc5ff;
	-webkit-animation: spinBG2 2s infinite linear;
	        animation: spinBG2 2s infinite linear;
}

div#rotatecircle li:nth-child(3){
	margin-left: 5px;
	margin-top: 15px;
	z-index: 1;
	width: 296px;
	height: 296px;
  border-width: 2px;
  position: absolute;
	box-shadow: inset 0px 0px 25px rgba(0, 123, 195, 0.507);
	-webkit-animation: spinBG 12s infinite linear;
	        animation: spinBG 12s infinite linear;
}

div#rotatecircle li:nth-child(4){
	z-index: 1;
	width: 330px;
	height: 330px;
	border-width: 5px;
  border-style: solid;
  position: absolute;
	box-shadow: inset 0px 0px 25px rgb(0 71 113);
	-webkit-animation: spinBG3 infinite linear;
	        animation: spinBG3 infinite linear;
}
