@-webkit-keyframes pulseGlow{
	0%  {text-shadow: 0px 0px 20px rgba(23, 160, 251, 0.75);}
	50% {text-shadow: 0px 0px 40px rgba(23, 122, 251, 0.5); }
	100%{text-shadow: 0px 0px 20px rgba(23, 167, 251, 0.75);}		
}
@keyframes pulseGlow{
	0%  {text-shadow: 0px 0px 20px rgba(23, 160, 251, 0.75);}
	50% {text-shadow: 0px 0px 40px rgba(23, 122, 251, 0.5); }
	100%{text-shadow: 0px 0px 20px rgba(23, 167, 251, 0.75);}		
}

@-webkit-keyframes pulseShadow{
	0%  {box-shadow: 0px 0px 30px rgba(23, 198, 251, 0.25);}
	50% {box-shadow: 0px 0px 30px rgba(23, 122, 251, 0.75);}
	100%{box-shadow: 0px 0px 30px rgba(23, 205, 251, 0.25);}		
}

@keyframes pulseShadow{
	0%  {box-shadow: 0px 0px 30px rgba(23, 198, 251, 0.25);}
	50% {box-shadow: 0px 0px 30px rgba(23, 122, 251, 0.75);}
	100%{box-shadow: 0px 0px 30px rgba(23, 205, 251, 0.25);}		
}

@-webkit-keyframes spinBG{
	0%  {transform: rotate(0deg);}
	100%{transform: rotate(360deg);}	
}

@keyframes spinBG{
	0%  {transform: rotate(0deg);}
	100%{transform: rotate(360deg);}	
}

@-webkit-keyframes spinBG2{
	0%{
		transform: rotate(360deg);
		box-shadow: 0px 0px 1px rgba(23, 198, 251, 0.5);
	}
	50%{
		transform: rotate(180deg);
		box-shadow: 0px 0px 20px rgba(23, 179, 251, 0.5);
	}
	100%{
		transform: rotate(0deg);
		box-shadow: 0px 0px 1px rgba(23, 179, 251, 0.5);
	}	
}

@keyframes spinBG2{
	0%{
		transform: rotate(360deg);
		box-shadow: 0px 0px 1px rgba(23, 198, 251, 0.5);
	}
	50%{
		transform: rotate(180deg);
		box-shadow: 0px 0px 20px rgba(23, 179, 251, 0.5);
	}
	100%{
		transform: rotate(0deg);
		box-shadow: 0px 0px 1px rgba(23, 179, 251, 0.5);
	}	
}

@-webkit-keyframes spinBG3{
	0%{
		transform: rotate(180deg);
		box-shadow: 0px 0px 1px rgba(23,246,251, 0.1);
	}
	50%{
		transform: rotate(0deg);
	}
	100%{
		transform: rotate(-180deg);
	}	
}

@keyframes spinBG3{
	0%{
		transform: rotate(180deg);
		box-shadow: 0px 0px 1px rgba(23,246,251, 0.1);
	}
	50%{
		transform: rotate(0deg);
	}
	100%{
		transform: rotate(-180deg);
	}	
}


