.navbar {
    background: #000000;
    height: 65px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 999;
    opacity: 1;
  }
  @-webkit-keyframes scrollDown {
    0% {
      height: 65px;
    }
  
    100% {
      height: 0px;
    }
  }
  @keyframes scrollDown {
    0% {
      height: 65px;
    }
  
    100% {
      height: 0px;
    }
  }
  
  @-webkit-keyframes scrollUp {
    0% {
      height: 0px;
    }
  
    100% {
      height: 65px;
    }
  }
  
  @keyframes scrollUp {
    0% {
      height: 0px;
    }
  
    100% {
      height: 65px;
    }
  }
  /* @keyframes scrollUp1 {
    0% {
      height: 0px;
    }
  
    100% {
      height: 85px;
    }
  } */
  .navbar-container {
    display: flex;
    justify-content: space-between;
    height: 65px;
  }
  .container {
    z-index: 1;
    width: 100%;
    max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    cursor: pointer;
    text-decoration: none;
    font-size: 2rem;
    display: flex;
    align-items: center;
  }
  
  .navbar-icon {
    margin-right: 0.5rem;
  }
  
  .nav-menu {
    grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    list-style: none;
    text-align: center;
    justify-content: flex-start;
  }
  
  .nav-item {
    height: 50px;
    border-bottom: 2px solid transparent;
  }
  
  /* .nav-item:hover {
    border-bottom: 2px solid #60bfff;
  }
   */
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    padding: 0.3rem 3rem;
    height: 100%;
  }
  
  .menu-icon {
    display: none;
  }
  @media screen and (min-width: 960px) {

    .navbar{
      overflow: hidden;
    }
  }
 
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 100vh;
      position: absolute;
      top: 64px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
      overflow-x: hidden;
    }
  
    .nav-menu.active {
      background: #000000;
      left: 0;
      opacity: 1;
      transition: all 0.6s ease;
      z-index: 1;
      margin: 0;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      color:  #60bfff;
      transform: scale(1.2);
      transition: all 0.3s ease;
    }
  
    .nav-item:hover {
      border: none;
    }
  
    .nav-item {
      width: 100%;
      height: 80px;
    }
    
    .navbar-logo {
      position: absolute;
      /* top: 0; */
      left: 0;
      transform: translate(15%, -5%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      /* top: 0; */
      right: 0;
      margin-right: 15px;
      /* margin-top: 15px; */
      transform: translate(15%, 15%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  
    .nav-btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 120px;
    }

  }
  
