  perspective: 1000px;
  transform: perspective(100px) rotatex(45deg) scale(2);
  position: relative;
}
.wrapper .fade {
  position: absolute;
  top: -50px;
  left: -40px;
  right: -40px;
  height: 400px;
  background: #000;
  background: linear-gradient(180deg, rgba(0, 0, 0, 1) 20%, rgba(0, 0, 0, 0) 100%);
  opacity: 1;
  z-index: 2;
}
